import React from 'react';
/*styles*/
import {ServiceDiv} from './styles'
/*animation*/
import {useScroll} from './useScroll';
import {scrollReview} from '../animation';
//img
import home2 from '../img/home2.png'
import clock from '../img/clock.svg';
import money from '../img/money.svg';
import diaphragm from '../img/diaphragm.svg';
import teamwork from '../img/teamwork.svg';

const ServiceSection  = () => {
    const [element, controls ] = useScroll();

    return (
        <ServiceDiv ref={element} variants={scrollReview} initial="hidden" animate={controls}>
            <div className="image">
                <img src={home2} alt=""/>
            </div>
            <div className="service-text">
                <h1>High <span>quality</span> service.</h1>
                <div className="services-items-container">
                    <div className="services-item">
                        <div className="title">
                            <img src={clock} alt=""/>
                            <span>Efficent</span>
                        </div>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. </p>
                    </div>
                    <div className="services-item">
                        <div className="title">
                            <img src={money} alt=""/>
                            <span>Affordable</span>
                        </div>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. </p>
                    </div>
                    <div className="services-item">
                        <div className="title">
                            <img src={diaphragm} alt=""/>
                            <span>Pro Grade Gear</span>
                        </div>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. </p>
                    </div>
                    <div className="services-item">
                        <div className="title">
                            <img src={teamwork} alt=""/>
                            <span>Teamwork</span>
                        </div>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. </p>
                    </div>
                </div>
            </div>
        </ServiceDiv>
    )
}



export default ServiceSection;