import React from 'react';
/*styles*/
import {FaqDiv} from './styles';
/*animation*/
import {motion, AnimateSharedLayout} from 'framer-motion';
import {showAnswer, scrollReview} from '../animation';
import {useScroll} from './useScroll';
/*components*/
import Toggle from './Toggle';

const FaqSection  = () => {
    const [element, controls ] = useScroll();

    const showAnswerHandler = (e) => {
        const showAnswer = e.target.nextElementSibling;

        showAnswer.classList.toggle('display')
    }
    return (
        <FaqDiv ref={element} variants={scrollReview} initial="hidden" animate={controls}>
            <h1>Any questions? <span>FAQ</span></h1>
            <div className="faq-items-container">
                <AnimateSharedLayout>
                    <motion.div layout className="faq-item">
                        <h2 onClick={showAnswerHandler}>How do I start?</h2>
                        <p  className="answer">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Accusamus doloremque facilis deserunt veritatis! Fuga aliquid, consequuntur libero voluptas explicabo deleniti repellendus. Eum nobis aspernatur minus cumque iusto soluta autem veritatis!</p>
                    </motion.div>
                    <motion.div layout className="faq-item">
                        <h2 onClick={showAnswerHandler}>What products do you offer?</h2>
                        <p className="answer">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Accusamus doloremque facilis deserunt veritatis! Fuga aliquid, consequuntur libero voluptas explicabo deleniti repellendus. Eum nobis aspernatur minus cumque iusto soluta autem veritatis!</p>
                    </motion.div>
                    <div layout className="faq-item">
                        <h2 onClick={showAnswerHandler}>Different payment methods</h2>
            
                        <motion.p layout className="answer">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Accusamus doloremque facilis deserunt veritatis! Fuga aliquid, consequuntur libero voluptas explicabo deleniti repellendus. Eum nobis aspernatur minus cumque iusto soluta autem veritatis!</motion.p>
                    
                    </div>
                    <div  className="faq-item">
                    
                        <Toggle title='Daily Schedule'>
                            <p >Tolele Lorem ipsum, dolor sit amet consectetur adipisicing elit. Accusamus doloremque facilis deserunt veritatis! Fuga aliquid, consequuntur libero voluptas explicabo deleniti repellendus. Eum nobis aspernatur minus cumque iusto soluta autem veritatis!</p>
                        </Toggle>
                    </div>

                    <div  className="faq-item">
                    
                        <Toggle title='Another FAQ'>
                            <p >Chubaka peperoni Lorem ipsum, dolor sit amet consectetur adipisicing elit. Accusamus doloremque facilis deserunt veritatis! Fuga aliquid, consequuntur libero voluptas explicabo deleniti repellendus. Eum nobis aspernatur minus cumque iusto soluta autem veritatis!</p>
                        </Toggle>
                    </div>
                </AnimateSharedLayout>
            </div>
        </FaqDiv>
    )
}



export default FaqSection;