import React from 'react';
import {MovieHeroDiv} from './styles';

const MovieHero = ({bkgimg, title, shortDescription}) => {
    return (
            <MovieHeroDiv style={{backgroundImage:`url(${bkgimg})`}}>
                <h1>{title}</h1>
                <h3>{shortDescription}</h3>
            </MovieHeroDiv>
    )
}

export default MovieHero;