import React from 'react';
import {Link} from 'react-router-dom';
/*style*/
import {MovieItemDiv} from './styles'
/*animation*/
import {motion} from 'framer-motion';
import {fade, imgAnimation, lineAnimation} from '../animation';


const MovieItem = ({id, title, img, url, movie}) => {
  console.log(img)
    return (
        <Link key={id} to={url}>
            <MovieItemDiv>
                <motion.h1 variants={fade}>{title}.</motion.h1>
                <motion.div  variants={lineAnimation} className="line"></motion.div>
                <div className="hide">
                    <motion.img variants={imgAnimation} src={img} alt={title}/>
                </div>
            </MovieItemDiv>
        </Link>
    )
}

export default MovieItem;