import styled from 'styled-components';
import {motion} from 'framer-motion';

//styled components
export const NavDiv = styled.nav`
    width: 100%;
    height: 90px;
    padding: 2rem 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center; 
    background-color: #262424;
    position: sticky;
    top:0;
    z-index: 99;

    .menu {
        width: 40%;
        display: flex;
        justify-content: space-between;

    }
    .menu-icon {
        font-size: 1.5rem;
        cursor: pointer;
    }
    .menu-mobile {
        height: 38vh;
        width: 100vw;
        position: absolute;
        z-index: 110;
        background-color: black;
        top: 0;
        left: 0;
        display: flex;
        flex-direction:column;
        
        .close-item{
            margin: 2rem 3rem;
            text-align: right;
            font-size: 1.5rem;
        }
        .menu-items {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }
`

export const AboutDiv = styled.div`
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 2rem;

    .about-text {
        .title {
            margin: 2rem 0;
            overflow: hidden;
            
            span {
                color: aquamarine;
            }
        }
        p {
            margin: .5rem;
            line-height: 30px;
        }
        button {
            border: 2px solid aquamarine;
            background-color: transparent;
            color: white;
            padding: .5rem 1rem;
            margin: 1rem 0;
        }
    }

    
    .img {
        width:40%;
        height: inherit;
     
        img {
            width: 100%;
            height: inherit;
            object-fit: cover;
        }
        
    }

    @media (max-width: 600px){
        display: flex;
        flex-direction: column;
        height: inherit;

        .about-text {

        }
        .img {
            width: 90%;
            height:inherit;
            margin-top: 1rem;
        }
    }
`
export const WaveDiv  = styled.div`
    position: absolute;
    z-index:-5;
`
export const ServiceDiv = styled(motion.div)`
width: 100%;
display: flex;
justify-content: space-around;
align-items: flex-start;
margin-bottom: 2rem;

.service-text {
    width:45%;

    h1 {
        margin: 5rem 0 2rem;

        span {
            color: aquamarine;
        }
    }
    .services-items-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .services-item {
            width: 35%;
            margin: 2rem;

            .title {
                display: flex;
                justify-content: left;
                align-items: center;
                margin-bottom: 1rem;

                span {
                    background-color: #c4b4b4;
                    color: black;
                    margin-left: 1rem;
                    padding: .5rem ;
                    border-radius: 5px;
                }
            }
            p {
                line-height: 23px;
            }
        }
    }
}

.image {
    width:50%;
    
    
}
@media (max-width: 600px){
   flex-direction: column;
   align-items: center;
   width: 80%;
   margin: 5rem auto;

   .image {
        width: 65%;
        margin: 1rem auto;
   }
   .service-text{
        width: 90%;

        .services-items-container {

            .services-item{
                width: 100%;
                margin: 1.5rem 0;
            }
        }
   }
}

`

export const FaqDiv = styled(motion.div)`
width: 100%;
margin-bottom: 2rem;

h1 {
    margin-bottom: 2rem;

    span {
        color: aquamarine;
        display:block;
    }
}
.faq-item {
    margin-bottom: 1.5rem;
    padding-bottom: 1.5rem;
    border-bottom: 2px solid white;

    h2 {
        margin-bottom: 1rem;
        cursor: pointer;
    }
    .answer {
       display: none;

    }
    .display {
       display: block;
       // transition: all 1s ease;
    

    }
}

`
export const PorfolioDiv = styled(motion.div)`
    background-color: lightgray;
    padding: 3rem 0;
`

export const MovieItemDiv = styled.div`
   width: 80%;
   margin: 2rem auto;

   h1 {
       margin-bottom: 1rem;
   }
   .line {
       width: 100%;
       height: 5px;
       background-color: #23d997;
       margin-bottom: 2rem;
   }
   .hide {
       overflow: hidden;
       
       img {
           width: 100%;
           height: 70vh;
           object-fit: cover;
           object-position: center;
       }
   }
`
export const Frame1 = styled(motion.div)`
    position:fixed;
    left: 0;
    top: 10%;
    width: 100%;
    height: 100vh;
    background: #fffebf;
    z-index: 2;
`
export const Frame2 = styled(Frame1)`
    background: #ff8efb;
`
export const Frame3 = styled(Frame1)`
    background: #8ed2ff;
`
export const Frame4 = styled(Frame1)`
    background: #8effa0;
`


export const MovieHeroDiv = styled.div`
    height: 80vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
        font-size: 5rem;
        margin-bottom:1.5rem;
    }
    h3 {
        font-size: 2rem;
    }

    @media (max-width:600px) {
        padding: 1rem;
        h1 {
             font-size: 3rem
        }
        h3 {
            font-size: 1rem;
        }
    }

`
export const MovieDetailsDiv = styled.div`
    height: 60vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 80%;
    margin: 5rem auto;

   .text, .image {
       width: 30%;
       height: inherit
   }
   .text {

        h3 {
            font-size: 2.5rem;
        }
        .line {
            width: 40%;
            height: 5px;
            background-color: grey;
            margin: 1.5rem 0;
        }

   }
   .image {
       height: inherit;

       img {
           height: inherit;
           object-fit: cover;
       }
   }

   @media (max-width:600px) {
        flex-direction: column;
        height: inherit;
        align-items: center;
        
        .text {
            width: 100%;
            margin-bottom: 1rem;
        }
    }

`
export const MovieFooterDiv = styled.div`
    height: 60vh;
    width: 80%;
    margin: 5rem auto;

    img {
        height: 80%;
        width:100%;
        object-fit: cover;
    }
    .awards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 4rem 0 2rem;

        .award-item {
            width: 30%;
            margin-left: .5rem;

            .line {
                width: 100%;
                height: 5px;
                background-color: aquamarine;
                margin: 1.5rem 0;
            }
        }
    }

    @media (max-width:600px) {
        
        
        .awards {
            flex-direction: column;
            .award-item {
                width: 100%;
                margin-bottom: 1rem;
            }
        }
    }
`
export const ContactDiv = styled(motion.div)`
    height: 100vh;
    width: 80%;
    margin: 5rem auto;
`