import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import { FiMenu } from "react-icons/fi";
import { AiOutlineCloseCircle } from "react-icons/ai";
/*styles*/
import {NavDiv} from './styles';

const Nav  = () => {
    const [isMobile, setIsMobile] = useState(false);
    const [openMenu, setOpenMenu] = useState(false);
    
    useEffect(() => {

        if(window.innerWidth <= 600){
            setIsMobile(true)
        }
       
    }, [])

    const openMobileMenu = () => {
        setOpenMenu(!openMenu)
    }
    
    return (
        <NavDiv>
            <div className="logo">
                <Link to="/">Rex</Link>
            </div>
            <div className="menu" style={isMobile ? {display: 'none'} : {display: 'flex'}}>
                <Link to="/">1. About</Link>
                <Link to="/portfolio">2. Portfolio</Link>
                <Link to="/contact">3. Contact Us</Link>
            </div>
            <FiMenu onClick={openMobileMenu} className="menu-icon" style={isMobile ? {display: 'inline-block'} : {display: 'none'}}/>
            <div className="menu-mobile" style={openMenu ? {display: 'flex'}: {display: 'none'}}>
                <div className="close-item">
                    <AiOutlineCloseCircle onClick={openMobileMenu}  style={{cursor: 'pointer'}}/>
                </div>
                <div className="menu-items">
                    <Link to="/" onClick={() => setOpenMenu(false)}>1. About</Link>
                    <Link to="/portfolio" onClick={() => setOpenMenu(false)}>2. Portfolio</Link>
                    <Link to="/contact" onClick={() => setOpenMenu(false)}>3. Contact Us</Link>
                </div>
            </div>
        </NavDiv>
    )
}

export default Nav;