import React, {useState, useEffect} from 'react';
import {useParams} from 'react-router-dom';
/*animation*/
/*animation*/
import {motion} from 'framer-motion' 
import {pageAnimation} from '../animation'
/*components*/
import MovieHero from '../components/MovieHero';
import MovieInfo from '../components/MovieInfo';
import MovieFooter from '../components/MovieFooter';
import {ScrollTop} from '../components/ScrollTop'

const Movie = ({data}) => {

let params = useParams();
const [movieSelected, setMovieSelected] = useState([])

useEffect(() => {
    const currentMOvie = data.filter(item => {
        let checkURL = item.url.split('/')[2]
        return   checkURL === params.movie
    })
    setMovieSelected(currentMOvie[0])
}, [params])


    return (
        <motion.div  variants={pageAnimation} initial="hidden" animate="show" exit="exit" className="container">
            <ScrollTop/>    
        {
            movieSelected && 
            (
                <>
                <MovieHero 
                    bkgimg={movieSelected.image}
                    title={movieSelected.title}
                    shortDescription={movieSelected.short}
                />
                <MovieInfo 
                    description={movieSelected.info}
                    img={movieSelected.thumbnail}
                />
                <MovieFooter 
                    img={movieSelected.image}
                    awards={movieSelected.awards}
                />
                </>
            
            )
        }

        </motion.div>
    )
}

export default Movie;