export const pageAnimation = {
    hidden: {
        opacity: 0,
        y: 300
    },
    show: {
        y:0,
        opacity: 1,
        transition: {
            duration: .5, 
            when: 'beforeChildren',
            staggerChildren: .25
        }
    },
    exit: {
        opacity: 0,
        y: 300,
        transition: {
            duration: .5, 
        }
    }
}

export const titleAnimation =  {
    hidden: {y: 200},
    show: {
        y:0,
        transition: {duration: .75, ease: "easeOut"}
    }
}

export const fade =  {
    hidden: {opacity: 0},
    show: {
        opacity: 1,
        transition: {duration: .5, ease: "easeOut"}
    }
}
export const imgAnimation =  {
    hidden: {scale: 1.5, opacity: 0},
    show: {
        opacity: 1,
        scale: 1,
        transition: {duration: .5, ease: "easeOut"}
    }
}
export const lineAnimation =  {
    hidden: {width: '0%'},
    show: {
        width: '100%',
        transition: {duration: 1.3, ease: "easeOut"}
    }
}
export const slider =  {
    hidden: {
        x: '-130%',
        skew: '45deg'
    },
    show: {
        x: '100%',
        skew: '0deg',
        transition: {duration: .7, ease: "easeOut"}
    }
}
export const sliderContainer = {
    hidden: {
       opacity: 1
    },
    show: {
        opacity: 1,
        transition: {staggerChildren: 0.15, ease: "easeOut"}
    }
}

export const showAnswer = {
    hidden: {
       opacity: 0
    },
    show: {
        opacity: 1,
        transition: {duration: .5, ease: "easeOut"}
    }
}

export const scrollReview = {
    hidden: {
       opacity: 0,
       scale: 1.2,
       transition: {
        duration: .5,
         ease: "easeOut"
        }
    },
    show: {
        opacity: 1,
        scale: 1,
        transition: {
            duration: 1,
             ease: "easeOut"
            }
    }
}