import React from 'react';
import {Link} from 'react-router-dom';
/*styles*/
import {AboutDiv, WaveDiv} from './styles'
/*animation*/
import {motion} from 'framer-motion';
import {titleAnimation} from '../animation'; 
import {fade} from '../animation'; 
import {imgAnimation} from '../animation'; 
/*components*/
import Wave from './Wave';
/*images*/
import home1 from '../img/home1.png'

const AboutSection  = () => {

 
    return (
        <AboutDiv>
            <div className="about-text">
                <motion.div  className="title">
                    {/* <motion.h1 animate={{ opacity:1, transition:{duration: 2}}} initial={{opacity:0}}>We work to make </motion.h1> */}
                    <motion.h1  variants={titleAnimation}>We work to make </motion.h1>
                    <motion.h1  variants={titleAnimation}>your <span>dreams</span></motion.h1>
                    <motion.h1  variants={titleAnimation}>como true.</motion.h1>
                </motion.div>
                <motion.p  variants={fade}>Contact us for any photography or videography ideas that you have.</motion.p>
                <Link to="/contact">
                    <motion.button variants={fade}>Contact Us</motion.button>
                </Link>
            </div>
            <div className="img">
                <motion.img 
                    variants={imgAnimation} 
                    src={home1} 
                    alt="Home 1"
                />
            </div>
            <WaveDiv>
                <Wave />
            </WaveDiv>
        </AboutDiv>
    )
}



export default AboutSection;