import React from 'react';
import {MovieDetailsDiv} from './styles';

const MovieInfo = ({description, img}) => {
    return (
        <MovieDetailsDiv>
            <div className="text">
                <h3>Briefing</h3>
                <div className="line"></div>
                <p>{description}</p>
            </div>
            <div className="image">
                <img src={img} alt=""/>
            </div>
        </MovieDetailsDiv>
    )
}

export default MovieInfo;